body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  text-decoration: none;
  color: inherit; }

.app {
  display: grid;
  background-color: #eee;
  color: #333;
  min-height: 100vh;
  max-width: 1000px;
  margin: auto; }

.bottom-bar {
  position: fixed;
  bottom: 0;
  display: grid;
  grid-auto-flow: column;
  width: 100%;
  max-width: 1000px;
  height: 50px;
  background-color: #444;
  color: white;
  place-items: center; }
  .bottom-bar a {
    color: inherit;
    text-decoration: none; }
  .bottom-bar .bottom-bar__item {
    text-transform: capitalize;
    padding: 8px; }

.not-found-page {
  display: grid;
  place-content: center;
  padding: 8px;
  min-height: 100vh; }

.home-page {
  padding: 8px; }

.users-list .users-list__item {
  display: grid;
  grid: auto / auto auto;
  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }

.login-page {
  padding: 8px; }

.user-page {
  padding: 8px; }

.users-detail .users-detail__item {
  display: grid;
  grid: auto / auto;
  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }
  .users-detail .users-detail__item p {
    margin: 0; }

.posts-list {
  padding: 8px; }
  .posts-list .posts-list__item {
    display: grid;
    grid: auto / auto;
    grid-gap: 4px;
    padding: 8px;
    cursor: pointer; }
  .posts-list .posts-list__title::first-letter {
    text-transform: uppercase; }

.albums-list {
  padding: 8px; }
  .albums-list .albums-list__item {
    display: grid;
