    grid-gap: 4px;
    padding: 8px;
    cursor: pointer; }

.posts-detail-page {
  padding: 8px; }

.posts-detail .posts-detail__item {
  display: grid;
  grid: auto / auto;
  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }
  .posts-detail .posts-detail__item p {
    margin: 0; }

.posts-detail .posts-detail__title {
  font-size: 26px; }
  .posts-detail .posts-detail__title::first-letter {
    text-transform: uppercase; }

.posts-detail .posts-detail__body {
  font-size: 18px; }
  .posts-detail .posts-detail__body::first-letter {
    text-transform: uppercase; }

.posts-detail .posts-detail__comments {
  padding: 8px; }

.comments-list .comments-list__item {
  display: grid;
  grid: auto / auto;
  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }
  .comments-list .comments-list__item p {
    margin: 0; }

.comments-list .comments-list__name::first-letter {
  text-transform: uppercase; }

.comments-list .comments-list__body {
  padding: 8px;
  background-color: #eaeaea; }

.user-page {
  padding: 8px; }

.photos .photos__item {
  display: grid;
