  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }
  .photos .photos__item p {
    margin: 0; }
  .photos .photos__item img {
    margin: auto;
    width: 100%; }

.user-page {
  padding: 8px; }

.photos-detail .photos-detail__item {
  display: grid;
  grid: auto / auto;
  grid-gap: 4px;
  padding: 8px;
  cursor: pointer; }
  .photos-detail .photos-detail__item p {
